export let code_ready = `
/* 
 * 
 * 
 * ...
 */

/*  */
*{
  transition:all 1.5s;
}
/*  */
body {
  background: #455A64;
}
/*  */
#code_body {
  width: 100%;
  border: 1px solid #CFD8DC;
  background: #CFD8DC;
  padding: 16px;
  overflow: auto;
}

/*  */
.token.comment, .token.punctuation {
  color: #757575;
}
.token.selector {
  color: #007400;
}
.token.property {
  color: #cf1f1f;
}

/* 
 * 
 */
.breathe {
  animation: breathe 1s ease 0s infinite;
}


/*  */

/*  */
#options {
  width: 32%;
}
/*  */
#paper {
  flex: 1;
  background: #fdfdfd;
  margin: 16px;
  overflow: auto;
  white-space: pre-wrap;
}

/*  */
`